<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-row align="center" justify="center">
        <v-subheader class="text-center">Entregar Cesta Básica</v-subheader>
      </v-row>
      <v-row align-center hidden>
        <v-col>
          <v-text-field v-model="entrega.cdEntrega" label="Id" outlined disabled></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="12" md="8" lg="6" xl="6">
          <v-combobox
          :items="solicitacoes"
          :hint="`${formatedHint(this.entrega.solicitacao.dtSolicitacao, 'Data da Solicitação: ' + this.entrega.solicitacao.dtSolicitacao)}`"
          persistent-hint
          v-model="entrega.solicitacao"
          item-text="codigo"
          item-value="cdSolicitacao"
          label="Solicitação - Código"
          placeholder="Escolha a Solicitação"
          return-object
          dense
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="12" md="8" lg="6" xl="6">
          <v-combobox
            :items="beneficiados"
            :hint="`${formatedHint(this.entrega.solicitacao.beneficiado.npf, 'Número de Pessoas na Família: ' + this.entrega.solicitacao.beneficiado.npf)}`"
            persistent-hint
            v-model="entrega.solicitacao.beneficiado"
            item-text="nome"
            item-value="cdUsuario"
            label="Beneficiado - Nome"
            placeholder="Escolha o Beneficiado"
            return-object
            dense
            disabled
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="12" md="8" lg="6" xl="6">
          <v-combobox
          :items="instituicoes"
          :hint="`${formatedHint(this.entrega.solicitacao.cdSolicitacao, this.entrega.solicitacao.instituicao.bairro.nome + ' - ' + this.entrega.solicitacao.instituicao.bairro.cidade.nome + ' - ' + this.entrega.solicitacao.instituicao.bairro.cidade.uf.nome)}`"
          persistent-hint
          v-model="entrega.solicitacao.instituicao"
          item-text="nome"
          item-value="cdUsuario"
          label="Instituição - Nome"
          placeholder="Escolha a Instituição"
          return-object
          dense
          disabled
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="12" md="8" lg="6" xl="6">
          <v-combobox
            :items="tpCestas"
            :hint="`${entrega.solicitacao.tpCesta.descricao}`"
            persistent-hint
            v-model="entrega.solicitacao.tpCesta"
            item-text="nome"
            item-value="id"
            label="Tipo de Cesta - Descricao"
            placeholder="Escolha o Tipo de Cesta"
            return-object
            dense
            disabled
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="12" md="8" lg="6" xl="6">
          <v-slider
            v-model="entrega.qtdCestas"
            type="number"
            min=1
            max=50
            :value="`${entrega.solicitacao.qtdCestas}`"
            thumb-label="always"
            dense
            outlined
            required></v-slider>
          <v-subheader dense>Qtd. Cestas Entregues {{ (entrega.solicitacao.qtdCestas)? '(Qtd. Solicitada: ' + entrega.solicitacao.qtdCestas + ')': ''}}</v-subheader>
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="12" md="8" lg="6" xl="6">
            <v-textarea
              v-model="entrega.documento"
              label="Beneficiado - Documento"
              hint="Número de qualquer documento com foto do Beneficiado"
              counter=150
              rows=3
              dense
              no-resize
              outlined
              required></v-textarea>
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="12" md="8" lg="6" xl="6">
            <v-btn color="success" @click="save">Salvar</v-btn>
          </v-col>
        </v-row>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/EntregaDataService";
import SolicitacaoDataService from "../service/SolicitacaoDataService";
import BeneficiadoDataService from "../service/BeneficiadoDataService";
import InstituicaoDataService from "../service/InstituicaoDataService";
import TipoCestaDataService from "../service/TipoCestaDataService";

export default {
  data: () => ({
    beneficiados: [],
    instituicoes: [],
    solicitacoes: [],
    tpCestas: [],
    entrega: {cdEntrega:0, qtdCestas:0, dtEntrega:'', solicitacao: {cdSolicitacao:0, codigo:'', dtSolicitacao:'', qtdCestas:0, status:0, tpCesta:{id:0,nome:'',descricao:''}, instituicao: {cdUsuario:0,nome:'',login:'',senha:'',cnpj:0,rua:'',numImovel:'',bairro:{id:0,nome:'',cidade:{id:0,nome:'',uf:{id:0,sigla:'',nome:''}}}}, beneficiado:{cdUsuario:0, npf:0, nome:''}}},
  }),
  computed: {
    id() {
      return this.$route.params.id;
    },
    now() {
      return new Date().toISOString()
    },
  },
  watch: {

  },
  methods: {
    formatedHint(cond, desc) {
      return (cond) ? desc : ''
    },
    initialize() {
      if(this.id > 0){
        DataService.retrieve(this.id).then(response => {
          this.entrega = response.data
        });
      }
      BeneficiadoDataService.retrieveAll().then(response => {
        this.beneficiados = response.data
      });
      InstituicaoDataService.retrieveAll().then(response => {
        this.instituicoes = response.data
      });
      SolicitacaoDataService.retrieveAll().then(response => {
        this.solicitacoes = response.data
      });
      TipoCestaDataService.retrieveAll().then(response => {
        this.tpCestas = response.data
      });
    },
    save(e) {
      e.preventDefault();
      if(this.id > 0){
        DataService.update(this.id, this.entrega, {
          id: this.id,
          entrega: this.entrega
        }).then(() => {
          this.$router.push('/entregas')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      } else {
        //console.log(this.solicitacao);
        this.entrega.dtEntrega = this.now
        DataService.create(this.entrega, {
          entrega: this.entrega
        }).then(() => {
          this.$router.push('/entregas')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      }
    }
  },
  created() {
    this.initialize()
  }
};
</script>

